import { useRouter } from 'next/router'
import Link from 'next/link'
import Navbar from '../../components/Navbar'

export default function Experience(){
  const router = useRouter()
  const { goal } = router.query
  const opts = [
    {id:'beginner', label:'Beginner (0-6 months)'},
    {id:'intermediate', label:'Intermediate (6-24 months)'},
    {id:'advanced', label:'Advanced (2+ years)'}
  ]
  return (
    <>
      <Navbar />
      <main className="container p-8 max-w-2xl">
        <h2 className="text-2xl font-semibold mb-4">Your training experience</h2>
        <p className="text-slate-600 dark:text-slate-300 mb-4">Select the option that best describes you.</p>
        <div className="grid md:grid-cols-2 gap-4">
          {opts.map(o => (
            <Link key={o.id} href={`/onboard/time?goal=${encodeURIComponent(goal)}&experience=${o.id}`} className="card">
              <h3 className="font-semibold">{o.label}</h3>
            </Link>
          ))}
        </div>
      </main>
    </>
  )
}
